@charset "utf-8";
*{margin:0px;padding:0px;}
body{
  font-family:"맑은 고딕",arial, sans-serif;
  font-size:13px;
  color:#333;
  background-color: #eff3f7;
}
a{
  color:#333;text-decoration:none;
  font-size:16px;
}
ul li{list-style:none;}

.loading_img{
  width:100%;height:100%;
  object-fit:cover;
  position:fixed;
  z-index:10000000;
}

.loader_icon{
  position:fixed;
  left:50%;top:50%;
  transform:translate(-50%, -50%);

}

@media screen and (min-width:1025px) {
  /* 상단헤더 서식 */
  .header{
    width:200px;height:100%;
    text-align:center;
    position:fixed;
    top:0px;left:0px;
    z-index:100;
  }
  h1{padding:70px 0px 50px 0px;}

  .navi{line-height:40px;}
  .navi a{text-transform: uppercase;font-weight:bold;}
  .navi a:hover{text-decoration:underline;}

  /* 메인서식  */
  section{
    width:100%;height:100%;
    position:fixed;
    top:0px;
    padding-left:200px;
    padding-top:50px;
    overflow:scroll;
    box-sizing:border-box;
  }
  section h2{
    padding-top:100px;
    text-align:center;
  }
  /* footer서식 */
  .footer{
    width:100%;line-height:40px;
    background:#333; 
    position:fixed;
    bottom:0px;left:0px;
  }
  .footer address{
    color:#fff;text-align:center;
    font-style:normal;
  }
}


/* 내비게이션 서식 */
.nav{
  padding-top:70px;
  text-align:center;  
}
.nav li{
  line-height:40px;
}
.nav a{
  text-decoration:none;
  font-size:16px;
  padding:5px 10px;
  color:#5d5d5d;
  text-shadow:0px 1px 1px #adadad;
}

footer{
  background:#333;
  color:#fff;
  text-align:center;
}
footer > address{
  line-height:50px;
  font-style:normal;
}

/* tablet버전 */
@media screen and (max-width:1024px) and (min-width:768px) {


}

/* 모바일 버전 */
@media screen and (max-width:767px) {
  header{ 
    width:100%;height:100px;
    text-align:center;
    position:fixed;
    top:0px;left:0px;
    z-index:100;
    background:#fff;
    box-shadow: 0px 3px 6px #ccc;
  }
  header h1{padding:10px 0px;}
  .navi ul{
    display:flex;
  } 
  .navi ul li{
    width:32%;
    text-align:center;
  }
  .navi ul li:first-child{display:none;}
  .navi ul li a{
    font-size:1rem;
    font-weight:bold;
  }
}
